html{
    --primary-color: rgb(20, 20, 75);
    --secondary-color: #fb4901;
    --white-color: rgb(233, 233, 255);
    --grey-color: #cfcfcf;
    background-color: rgb(233, 233, 255);
    background-color: var(--white-color);
}
::after, ::before, *{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}
body{
    position: relative;
    max-width: 100%;
    min-height: 100vh;
    background-color: rgb(233, 233, 255);
    background-color: var(--white-color);
    color: rgb(20, 20, 75);
    color: var(--primary-color);
    font-family: 'Open Sans', sans-serif;
    text-align: center;
}
a, a:hover{
    text-decoration: none;
    color: rgb(20, 20, 75);
    color: var(--primary-color);
    transition: 0.3s ease-in-out;
}
img{
    max-width: 100%;
}
input{
    outline: none;
    border: none;
}
nav{
    margin: 0px 50px 100px 200px;
}
/* width */
::-webkit-scrollbar {
    width: 8px;
    background-color: rgba(255, 255, 255, 0);
}
/* Track */
::-webkit-scrollbar-track {
    background-color: rgb(20, 20, 75);
    background-color: var(--primary-color);
    border-left: 1px solid rgb(20, 20, 75);
    border-left: 1px solid var(--primary-color);
    border-right: 1px solid rgb(20, 20, 75);
    border-right: 1px solid var(--primary-color);
}
/* Handle */
::-webkit-scrollbar-thumb {
    background-color: #fb4901;
    background-color: var(--secondary-color);
    border-radius: 205px;
    cursor: pointer;
}
