.mossane-btn{
    outline: none;
    border: none;
    background-color: var(--secondary-color);
    padding: 10px 20px;
    padding-right: 30px;
    color: var(--white-color);
    font-weight: bold;
    cursor: pointer;
    border: 2px solid var(--secondary-color);
    transition: 0.5s ease-in-out;
}
.mossane-btn:hover{
    background-color: #18070094;
    color: var(--secondary-color);
}
.mossane-btn-anim{
    position: absolute;
    transition: 0.4s ease-in-out;
    top: 50%;
    right: 15px;
    -webkit-transform: translate(0px, -50%);
            transform: translate(0px, -50%);
    
}
.mossane-btn:hover>.mossane-btn-anim{
    -webkit-transform: translate(5px, -50%);
            transform: translate(5px, -50%);
}
.mossane-header {
  position: relative;
  width: 100%;
  height: 100vh;
  z-index: 500;
  background-image: url(/mossane-patrimoine/static/media/background.3a290b35.webp);
  background-position: left top;
  background-repeat: no-repeat;
  background-size: cover;
}

.mossane-header>hgroup,
.mossane-header>p {
  -webkit-transform: translateY(-75px);
          transform: translateY(-75px);
}

.mossane-header>hgroup>h1,
.mossane-header>hgroup>h2 {
  font-family: "Open Sans" !important;
}

.mossane-navbar {
  position: fixed;
  left: 0%;
  top: 0%;
  padding: 0% 10%;
  transition: 0.4s ease-in-out;
  z-index: 1000;
}

.mossane-navbar-reveal {
  background-color: var(--grey-color);
  box-shadow: 0px 0px 15px black;
  padding: 0% 2%;
}

.mossane-navbar-reveal>#logo {
  width: 40px;
}

.mossane-link {
  list-style: none;
  padding: 10px;

}

.mossane-link:hover>a,
.mossane-link:hover {
  color: var(--secondary-color);
}

.mossane-link-active>a,
.mossane-link-active {
  color: var(--secondary-color);
}

#logo {
  width: 50px;
  transition: 0.3s ease-in-out;
}

.mossane-dropdown {
  position: relative;
}

.mossane-dropdown-hover {
  position: absolute;
  top: 100%;
  left: 50%;
  z-index: -1;
  -webkit-transform: translate(-50%, -100%);
          transform: translate(-50%, -100%);
  color: var(--white-color);
  width: 500px;
  opacity: 0;
  transition: 0.5s ease-in;
}

.mossane-dropdown>ul>.mossane-link>a,
.mossane-dropdown>ul>.mossane-link {
  color: var(--white-color);
}

.mossane-dropdown:hover .mossane-dropdown-hover {
  z-index: 100;
  -webkit-transform: translate(-50%, 0%);
          transform: translate(-50%, 0%);
  background-color: var(--primary-color);
  opacity: 1;
}

.mossane-dropdown>div>ul>.mossane-link>a:hover,
.mossane-dropdown>div>ul>.mossane-link:hover {
  color: var(--secondary-color);
}

.mossane-dropdown-hover>ul>a>.mossane-link {
  color: white;
  transition: 0.4s ease-in-out;
}

.mossane-dropdown-hover>ul>a>.mossane-link:hover {
  color: var(--secondary-color);
}

#mossane-menu-mobile {
  display: none;
}

#mossane-btn-burger {
  position: fixed;
  display: none;
  z-index: 2000;
  z-index: 200;
  right: 15px;
  top: 30px;
  height: 5px;
  width: 35px;
  background-color:
    /* var(--primary-color) */
    red;
  overflow: visible;
  cursor: pointer;
}

#mossane-btn-burger::after {
  position: absolute;
  background-color:
    /* var(--primary-color) */
    red;
  z-index: 100;
  content: "";
  top: 200%;
  left: 0%;
  width: 100%;
  height: 100%;
}

#mossane-btn-burger::before {
  position: absolute;
  background-color:
    /* var(--primary-color) */
    red;
  z-index: 100;
  content: "";
  top: -200%;
  left: 0%;
  width: 100%;
  height: 100%;
}

#mossane-menu-mobile {
  position: fixed;
  margin: 0;
  z-index: 100;
  top: 0px;
  width: 100vw;
  height: 100vh;
  transition: 0.5s ease-in-out;
  background-color: var(--primary-color);
  flex-direction: column;
  justify-content: center;
}

.mossane-menu-mobile-open {
  right: 1000px;
}

.mossane-menu-mobile-close {
  right: 0px;
}

.mossane-divider {
  width: 40%;
}

