}

@media screen and (max-width: 992px) {
  #logo {
    width: 50px;
    position: fixed;
    left: 15px;
    top: 5px;
  }

  #mossane-menu-mobile {
    display: flex;
    z-index: 1000;
  }

  .mossane-navbar {
    display: none;
  }

  .mossane-link>a,
  .mossane-link {
    color: var(--white-color);
  }

  .mossane-link-active>a,
  .mossane-link-active {
    color: var(--secondary-color);
  }

  .mossane-dropdown-hover {
    position: relative;
    top: 0%;
    left: 0%;
    z-index: 10;
    -webkit-transform: translate(0%, 0%);
            transform: translate(0%, 0%);
    color: var(--white-color);
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
    height: -webkit-max-content;
    height: max-content;
    opacity: 1;
  }

  .mossane-dropdown-hover>ul {
    flex-wrap: wrap;
  }

  .mossane-dropdown:hover .mossane-dropdown-hover {
    z-index: 10;
    -webkit-transform: translate(0%, 0%);
            transform: translate(0%, 0%);
    background-color: var(--primary-color);
    opacity: 1;
  }

  #mossane-btn-burger {
    display: flex;
    z-index: 5000;
  }

  hgroup>h1 {
    font-size: 3em;
  }

  hgroup>h2 {
    font-size: 1.8em;
  }

  hgroup+p {
    font-size: 1em;
  }
}
